function cfinput(data, flip) {
  console.log("got data", data, flip);
  if (flip) {
    console.log("Working with Config Packs");
    addConfigPacks(data);
  } else {
    console.log("Working with Rules");
    addRules(data);
  }
}

function addRules(data) {
  console.log(data);
  data = data.map((element) => element.data.NAME);
  data = [...new Set(data)];
  data.forEach((element) => {
    console.log("got element");
    console.log(element);
    ruleFactory(element);
  });
}

function addConfigPacks(data) {
  console.log(data);
  printCloudformation();
  data = data.map((element) => {
    delete element.data.NAME;
    delete element.data.TOTAL_RULES;
    delete element.data.URL;
    return Object.keys(element.data);
  });
  data = [].concat.apply([], data);
  data = [...new Set(data)];
  console.log(data);
  data.forEach((element) => {
    console.log("got element");
    console.log(element);
    ruleFactory(element);
  });
}

function printCloudformation() {
  console.log(cloudformationtemplate);
  console.log(JSON.stringify(cloudformationtemplate));
}

function ruleFactory(rule) {
  //   let rule = configFactory[rule]();
  cloudformationtemplate["Resources"][
    rule.replaceAll("_", "").toLocaleLowerCase()
  ] = configFactory[rule]();
  printCloudformation();
}

